
















import Vue from "vue";
import NaviPopup from "@/components/NaviPopup.vue"; // @ is an alias to /src

export default Vue.extend({
    components: {
        NaviPopup
    },
    data: function () {
        return {
            red: 255,
            green: 255,
            blue: 255,
            hexT: "ffffff"
        }
    },
    computed: {
        hex: {
            get: function () {
                let r = this.$data.red.toString(16), g = this.$data.green.toString(16), b = this.$data.blue.toString(16);
                let res = r + g + b;
                this.hexT = res;
                return res;
            },
            set: function (val: string) {
                this.red = parseInt(val.substr(0,2),16);
                this.green = parseInt(val.substr(2,2),16);
                this.blue = parseInt(val.substr(4,2),16);
            }
        }
    },
    watch: {
        hexT(val) {
            if(/^[0-9a-f]{6}$/i.test(val)) {
                this.hex = val;
            }
        }
    }
})
